'use strict';

export default class MediaSelectDirective {

  constructor() {
    this.restrict = 'E';
    this.replace = true;
    this.templateUrl = 'home/views/med.select.html';
    this.scope = {};
    this.transclude = false;
    this.bindToController = {
      'labeldir': '=',
      'formatResourceDir':'=',
      'formatDir':'='
    };

    this.controller = () => {};
    this.controllerAs = 'vm';
  }


  static directiveFactory() {
    var instance = new MediaSelectDirective();
    return instance;
  }

}
